export default { 
	props: {
		/**
		* 自动获取焦点
		*/
		current: {
			type: [Number, String],
			default: 0
		},
		// 是否自动播放
		autoplay: {
			type: [Boolean],
			default: false
		},
		// 是否显示面板指示点
		indicatorDots: {
			type: [Boolean],
			default: false
		},
		// 图片列表
		picList: {
			type: [Array],
			default: []
		},
	},
	data() {
		return {
			duration: 500,
			circular: true,
			isShowSwiper: false
		};
	},
	methods: {
		clickPic(index) {
			// this.current = index;
			// this.isShowSwiper = true;
			this.$emit('clickPic')

		},
		changeSwiper(e) {
			this.current = e.target.current;
			this.$emit('changeSwiper', e)
		}
	}
};
